@mixin flex-center-all {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
}

@mixin no-edges {
    margin: 0;
    padding: 0;
}

@mixin gender-button {
    font-family: $body-font;
    text-transform: uppercase;
    margin: 0 5px;
    width: 5.5em;
    height: 2em;
    font-weight: 400;
    border-radius: 10px;
}

@mixin gender-bg {
    &.parent-female{
    background-color: rgba($bg-female, .5);
    }
    &.parent-male{
    background-color: rgba($bg-male, .5);
    }
    &.parent-genderless{
    background-color: rgba($bg-ng, .5);
    }
}