@import 'normalize';
@import 'colors';
@import 'typography';
@import 'variables';
@import 'mixins';

body {
  @include no-edges;
  background-color: $bg-light;
  font-family: $body-font;
}
h1, h2, h3, h4, h5 {
  font-family: $header-font;
  margin: 0;
}
h1 {
  font-size: 3em;
}
h3 {
  margin: .5em 0;
}
main {
  display: flex;
  flex-flow: column nowrap;
  background-color: $bg-bold;
  position: relative;
  width: 100%;
  min-height: 100vh;
  margin: 0 auto;

  header.intro {
    @include flex-center-all;
    height: 10em;
    text-align: center;
    color: $text-light;
  }
  header.main-head {
    box-sizing: border-box;
    height: 4em;
    width: 100%;
    background-color: $bg-bold;
    color: $text-light;
    padding: 1em;
  }
}
.back-btn {
  width: 100%;
  button {
    width: 100%;
  }
}
.main-body {
  padding: 0 .5em;
}
.pokebreeder {
  display: flex;
  flex-flow: column nowrap;
  min-height: 100vh;
  background-color: $bg-light;
  padding-bottom: 7em;
  .count-box {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2em;
    transition: all ease-in .5s;
    background-color: $bg-bold;
    display:flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    margin-top: auto;
    box-sizing: border-box;
    padding: .5em .5em 6em .5em;
    color: $text-light;
    .saved-list {
      ul{
        @include no-edges;
        width: 100%;
        height: 24em;
        margin: .5em 0 5em 0;
        li {
          box-sizing: border-box;
          padding: .25em;
          width: 100%;
          text-align: left;
          display: flex;
          flex-flow: row nowrap;
          justify-content: space-between;
          height: 2em;
          background-color: lighten($bg-bold, 10%);
        }
        li:nth-of-type(2n){
          background-color: $bg-bold;
        }
      }
    }
  }
}
.count-box.shift-height {
  transition: all ease-in .5s;
  height: 32em;
}
.count-box::before{
  display:block;
  position: absolute;
  top: -1em;
  left: calc(50% - 2em);
  background-color: $bg-bold;
  width: 4em;
  height: 1em;
  border-radius: 10px 10px 0 0;
  content: '=';
  text-shadow: 2px 2px 2px $text-dark;
  text-align:center;
  color: $text-light;
  cursor: pointer;
}
.remove{
  font-weight: bold;
  font-family: $header-font;
  margin: 0;
  padding: .25em .5em;
  background-color: $bg-accent;
  border-radius: 50%;
  cursor: pointer;
}
.error-msg {
  font-size: .9em;
  color: $bg-accent;
  font-weight: 700;
  margin-bottom: .5em;
}
button {
  height: 3rem;
  padding: 0 1rem;
  background-color: $bg-accent;
  color: $text-light;
  font-family: $header-font;
  font-weight: 700;
  border: none;
  cursor: pointer;
  &.btn-small{
    background-color: $bg-light;
    color: $text-dark;
    height: 1.5em;
  }
}
.sort-links {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  margin: .5em 0;
  color: $header-bold;
  font-size: .9em;
  font-weight: 900;
  text-transform: uppercase;
  a{
    margin: 0 .25em;
    display: inline-block;
    width: 100%;
    text-align: center;
    height: 100%;
    padding: .25em;
    border: 1px dashed lighten($text-dark, 30%);
    color: lighten($text-dark, 30%);
    border-radius: 5px;
  }
  .sorted {
    border: 1px solid;
    color: $header-bold;
  }
}
.page-buttons {
  @include no-edges;
  width: 100%;
  @include flex-center-all;
  &:last-child button{
    width: 4em;
    height: 1.5em;
    text-align: center;
    border-radius: 10px;
  }
  button {
    @include flex-center-all;
    background-color: $bg-bold;
    border-radius: 50%;
    width: 2em;
    height: 2em;
    &.selected {
      background-color: $bg-accent;
      text-decoration: underline;
    }
  }
}
footer {
  position: fixed;
  box-sizing: border-box;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  padding: .5em;
  background-color: $bg-bold;
  a{
    @include flex-center-all;
    margin-right: .5rem;
    font-family: $header-font;
    font-weight: 900;
    font-size: 3rem;
    height: 3.5rem;
    width: 3.5rem;
    border-radius: 50%;
    background-color: $bg-accent;
    color: $text-light;
    text-decoration: none;
  }
}

.instructions {
  text-align: center;
  color: $text-light;
  ol {
    font-size: 1.3rem;
    margin: 2rem auto;
    width: 60%;
    text-align: left;
    li {
      margin-bottom: 1rem;
    }
  }
}

#infoText {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100vh;
  background-color: rgba($bg-dark, .5);
  .close{
    box-sizing: border-box;
    @include no-edges;
    @include flex-center-all;
    position: fixed;
    z-index: 100;
    top: 25.5vh;
    right: calc(10% + .25em);
    padding: .5em .5em;
    background-color: $bg-accent;
    border-radius: 50%;
    width: 2rem;
    height: 2rem;
    color: $text-light;
    font-family: $header-font;
    font-size: 1.5rem;
    font-weight: 700;
  }
  .info {
    box-sizing: border-box;
    position: fixed;
    top: 25vh;
    left: 10%;
    width: 80%;
    height: 50vh;
    overflow-y: scroll;
    padding: 1em;
    z-index: 100;
    background-color: $bg-light;
    font-size: .9rem;
    .copy {
      font-size: .8rem;
    }
  }
}

ul {
  @include no-edges;
  list-style-type: none;
  li {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    &:nth-child(2n){
      background-color: lighten($bg-light, 10%);
    }
  }
}
ul.type-list{
  list-style-type: none;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  text-align: center;
  li {
    display: inline-block;
    width: 32%;
    border-radius: 10px;
    color: $text-light;
    padding: .5em 0;
    margin-bottom: .25em;
    cursor: pointer;
    &:hover {
      text-shadow: 2px 2px 2px $text-dark;
    }
  }
}
button[data-gender='d'] {
  background-color: $bg-ng;
  pointer-events: none;
  background-color: $bg-light;
  border: 1px solid $bg-dark;
  color: $text-dark;
  @include gender-button;
}
button[data-gender='genderless'] {
  background-color: $bg-ng;
  @include gender-button;
}
button[data-gender='male'] {
  background-color: $bg-male;
  @include gender-button;
}
button[data-gender='female'] {
  background-color: $bg-female;
  @include gender-button;
}
.button-set {
  display: flex;
  margin: 0 0 0 auto;
  flex-flow: row nowrap;
  justify-content: flex-end;
}
.fancy-parent-container {
  display:flex;
  flex-flow: row wrap;
  justify-content: space-around;
  hr {
    width: 80%;
  }
  .fancy-parent {
    cursor: default;
    position: relative;
    text-align: center;
    &.clickable {
      cursor: pointer;
    }
    img {
    background-color: darken($bg-light, 10%);
    border-radius: 50%;
    background-size: 80% 80%;
    width: 96px;
    height: 96px;
    @include gender-bg;
    }
    .parent-name {
      position: absolute;
      left: 0;
      bottom: -.8em;
      width: 100%;
      font-size: .8em;
      font-weight: 700;
    }
    .gender-symbol {
      color: $text-dark;
      position: absolute;
      right: 0;
      bottom: 5px;
      font-size: 1.2em;
      text-shadow: 0px 1px 0px $text-dark, 1px 0px 0px $text-dark;
    }
  }
}
.parent-box {
  position: relative;
  min-height: 160px;
  margin: .5em 0;
  padding: .5em;
  background-color: lighten($bg-light, 5%);
  border: 1px inset;
  border-radius: 5px;
  text-align: right;
  img {
    float: left;
    background-color: rgba($bg-dark, .1);
    border-radius: 50%;
    background-size: 80% 80%;
    width: 144px;
    height: 144px;
  }
  & h4 {
    position: absolute;
    bottom: 0;
    left: 0;
    margin: .5em;
  }
  & h3 {
    position: absolute;
    top: 0;
    right: 5px;
    margin-top: 0;
    font-size: 2em;
  }
  & li{
    justify-content: flex-end;
  }
  .parent-types {
    margin-top: 2.5em;
  }
  .type-label-sm {
    display: inline;
    padding: .25em;
    margin: 3em .25em;
    border-radius: 5px;
    color: $text-light;
    text-shadow: 0px 1px 0px $text-dark, 1px 0px 0px $text-dark;
  }
  .parent-gender{
    margin: 0;
    padding: .5em;
    position: absolute;
    bottom: 0;
    right: 0;
    border-radius: 0 0 5px 0;
    font-weight: 900;
    text-transform: uppercase;
    @include gender-bg;
  }
}
.big-text {
  font-size: 4em;
  font-weight: bold;
  font-family: $header-font;
  color: $bg-accent;
  @include no-edges;
  @include flex-center-all;
  &:nth-child(4){
    width: 100%;
    margin-top: 1em;
  }
}
.baby-info-container {
  border: 1px solid $bg-bold;
  border-radius: 10px;
  padding: .5em;
  margin: 1em auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  h5 {
    grid-column: 1 / 3;
    img {
      vertical-align: middle;
    }
  }
  h3.baby-label {
    grid-column: 1 / 3;
    text-align: right;
    box-sizing: border-box;
    font-size: 2rem;
    font-weight: 900;
    margin: 0;
  }
}
.baby-container {
  display: flex;
  flex-flow: column nowrap;
  position: relative;
  align-items: stretch;
  margin: 0 auto;
  text-align: center;
  p {
    @include no-edges;
  }
  .baby-img {
    align-self: center;
    box-sizing: border-box;
    width: 144px;
    height: 144px;
    background-color: darken($bg-light, 10%);
    border-radius: 50%;
  }
  .type-label-sm{
    box-sizing: border-box;
    color: $text-light;
    border-radius: 5px;
    width: 100%;
    padding: .25em;
  }
}
.baby-info {
  padding-left: .5em;
  position: relative;
  .is-baby {
    box-sizing: border-box;
    position: absolute;
    right: 0;
    bottom: 0;
    font-size: .9rem;
    font-weight: 700;
    background-color: pink;
    padding: .25em;
    border-radius: 5px;
  }
}

.hidden {
  display: none;
}
